<template lang="html">
  <div class="Product-detail">
    <Row type="flex" justify="space-around" class="code-row-bg personal-detail">
      <Col span="18">
        <span class="font-16">{{$t("common.Out_Of_Stock_product")}}</span>
      </Col>
      <Col span="2" v-if="page_key == 'examine'">
        <Button type="primary" @click="back_off ('Product_list')">{{$t("common.Adopt")}}</Button>
      </Col>
      <Col span="2" v-if="page_key == 'examine'">
        <Button type="warning" @click="back_off ('Product_list')">{{$t("common.Overrule")}}</Button>
      </Col>
      <Col span="4" v-if="page_key == 'Already'">
        <Button type="warning" @click="back_off ('Already_list')">{{$t("common.Out_Of_Stock")}}</Button>
      </Col>
    </Row>
    <Row>
      <div class="project">
        <div class="project-name">
          <div class="clearfix">
            <div class="pro-logo left">
              <img :src="imgSrc" alt="">
            </div>
            <p class="pro-name left"> {{$t("common.Project_name")}}：{{product_number}}</p>
          </div>
          <ul class="clearfix financing-amount">
            <li>
              <p>{{$t("common.Financing_Amount")}}</p>
              <span>600,000</span>
            </li>
            <li>
              <p>{{$t("common.Credit_Rating")}} &nbsp&nbsp&nbsp&nbsp&nbsp&nbsp<i></i></p>
              <span>AA+</span>
            </li>
            <li>
              <p>{{$t("common.Annualized_interest_rate")}}</p>
              <span>3.2</span>
            </li>
            <li>
              <p>{{$t("common.Borrowing_period")}}</p>
              <span>6</span>
            </li>
          </ul>
          <ul class="financing-details clearfix" :class="{'financing-details-en': is_en}">
            <li>
              <span>{{$t("common.Repayment_method")}}：</span>
              <span>{{$t("common.Equal_amoun")}}</span>
            </li>
            <li>
              <span>{{$t("common.Third_party")}}：</span>
              <span>{{$t("common.Yes")}}</span>
            </li>
            <li>
              <span>{{$t("common.Minimum_amount")}}：</span>
              <span>10,000  {{$t("common.RMB")}}</span>
            </li>
            <li>
              <span>{{$t("common.Maximum_amount")}}：</span>
              <span>600,000  {{$t("common.RMB")}}</span>
            </li>
            <li>
              <span>{{$t("common.Amount_invested")}}：</span>
              <span>0  {{$t("common.RMB")}}</span>
            </li>
            <li>
              <span>{{$t("common.Investment_amount")}}：</span>
              <span>600,000  {{$t("common.RMB")}}</span>
            </li>
            <li>
              <span>{{$t("common.Estimated_earnings")}}： </span>
              <span>80,000  {{$t("common.RMB")}}</span>
            </li>
            <li class="clearfix">
              <span class="left">{{$t("common.Financing_progress")}}：</span>
              <Progress :percent="25" class="left" style="width:188px;"></Progress>
            </li>
          </ul>
        </div>
      </div>
      <div class="project-info">
        <Row>
          <Col span="6">
            <span>{{$t("common.Deadline")}}：</span>
            <span>2017-10-27 12:00</span>
          </Col>
          <Col span="6">
            <span>{{$t("common.Interest_rate")}}：</span>
            <span>{{$t("common.Daily_interest_rate")}}</span>
          </Col>
          <Col span="6">
            <span>{{$t("common.Number_of_guarantors")}}：</span>
            <span>1 </span>
          </Col>
          <Col span="6">
            <span>{{$t("common.Project_risk_assessment")}}：</span>
            <a href="javascript:;">{{$t("common.View")}}</a>
          </Col>
        </Row>
      </div>
      <div class="loan-introduction">
        <p>{{$t("common.project_introduction")}}：</p>
        <p>{{$t("common.project_introduction_A")}}</p>
      </div>
      <div class="loan-introduction">
        <p>{{$t("common.Additional_information")}}：</p>
        <p>{{$t("common.project_introduction_A")}}</p>
      </div>
    </Row>
  </div>
</template>

<script>
export default {
  data (){
    return {
      page_key:'examine',
      imgSrc:'/static/img/u1.png',
      product_number:'DG6201710160001',
      is_en:false
    }
  },
  methods:{
    back_off (index){
      this.$router.push('/'+index);
    },
    getParams () {
        // 取到路由带过来的参数
        let routerParams = this.$route.params.send_data
        let clickData = this.$route.params.click_data
        // 将数据放在当前组件的数据内
        if(routerParams){
          this.page_key = routerParams
        }
        if(clickData){
          this.imgSrc = clickData.brand
          this.product_number = clickData.product_number
        }
    }
  },
  mounted(){
    this.getParams()
    if (this.$i18n.locale == 'en') {
      this.is_en = true
    }
  }
}
</script>

<style lang="css">
.Product-detail {
  text-align: center;
  background: #fff;
  padding: 20px;
}
.Product-detail .personal-detail{
  height: 50px;
  background-color: rgba(242, 242, 242, 1);
  line-height: 50px;
  text-align: left;
}
.Product-detail .personal-detail button{
  height: 35px;
}

.project{
  margin-top: 20px;
}
.project-name{
  border: 1px solid rgba(188, 188, 188, 1);
  padding: 10px 0 30px 15px;
}

.pro-logo{
  width: 104px;
  height: 32px;
  border: 1px solid #f2f2f2;
  margin-right:30px;
}
.pro-logo img{
  width: 104px;
  height: 32px;
  display: block;
}
.pro-name{
  height: 32px;
  font-size: 16px;
  font-weight: 700;
  text-align: left;
  line-height: 32px;
}
.financing-amount{
  margin-top: 10px;
}
.financing-amount li{
  float: left;
  height: 84px;
  width: 280px;
  border-right: 1px solid rgba(188, 188, 188, 1);
  font-size: 16px;
  line-height: 40px;
  position: relative;
}
.financing-amount li:last-child{
  border-right: none;
}
.financing-amount li span{
  font-size: 24px;
  font-weight: bold;
  color: #f29100;
}
.financing-amount li i{
  width: 28px;
  height: 20px;
  background: url(../../assets/u6.png) no-repeat 0 0;
  background-size: 100%;
  position: absolute;
  top: 11px;
  right: 75px;
}
.financing-details{
  margin-left: 20px;
  margin-top: 25px;
}
.financing-details li{
  float: left;
  width: 280px;
  height: 30px;
  line-height: 30px;
  text-align: left;
  margin-bottom: 5px;
}
.Product-detail .financing-details-en li{
  width: 500px;
}
.available-balance{
  height: 47px;
  background-color: rgba(242, 242, 242, 1);
  text-align: center;
  line-height: 47px;
}

.project-info{
  border-bottom: 1px solid rgba(188, 188, 188, 1);
  padding-bottom: 15px;
  margin-top: 25px;
}
.project-info ul{
  margin-left: 40px;
}
.project-info ul li{
  float: left;
  margin-right: 145px;
}
.loan-introduction{
  margin-top: 25px;
  border-bottom: 1px solid rgba(188, 188, 188, 1);
  padding-bottom: 15px;
  text-align: left;
  padding-left: 40px;
}
</style>



